import React, { Component } from 'react';
import { base } from 'nc-lightapp-front';
const { NCModal, NCTable, NCButton } = base;
import Utils from '../utils';
const { langCheck } = Utils;
require('./index.less');

export default class ShareDetailModal extends Component {
	constructor(props) {
		super(props);
		this.state = {
			showShareDetailModal: false
		};
	}

	closeShareDetailModal() {
		this.setState({
			showShareDetailModal: false
		});
	}

	render() {
		const { data } = this.props;
		const columns = [
			{
				title: langCheck('reportMultiLang', '100301-000070'),/*  */
				dataIndex: 'jobId',
				key: 'jobId',
				width: 50
			},
			{
				title: langCheck('reportMultiLang', '100301-000071'),/*  */
				dataIndex: 'subject',
				key: 'subject',
				width: 150
			},
			{
				title: langCheck('reportMultiLang', '100301-000082'),/*  */
				dataIndex: 'createTime',
				key: 'createTime',
				width: 150
			},
			{
				title: langCheck('reportMultiLang', '100301-000072'),/*  */
				dataIndex: 'status',
				key: 'status',
				width: 150,
				render: (text, record, index) => {
					let html = '';
					switch (text) {
						case 6:
							html = langCheck('reportMultiLang', '100301-000074');/*  */
							break;
						case 7:
							html = langCheck('reportMultiLang', '100301-000076');/*  */
							break;
						case 9:
							html = langCheck('reportMultiLang', '100301-000077');/*  */
							break;
					}
					return <span>{html}</span>;
				}
			}
		];
		return (
			<NCModal show={this.state.showShareDetailModal} id="shareDetailModal" className="report-modal-class">
				<NCModal.Header>
					<NCModal.Title fieldid={langCheck('reportMultiLang', '100301-000028')}>{langCheck('reportMultiLang', '100301-000028')}</NCModal.Title>{/*  */}
				</NCModal.Header>

				<NCModal.Body>
					<NCTable
						columns={columns}
						data={data}
						scroll={{ y: 255 }}
					/>
				</NCModal.Body>
				<NCModal.Footer>
					<NCButton className="cancel-button" onClick={this.closeShareDetailModal.bind(this)} shape="border">
						{langCheck('reportMultiLang', '100301-000048')}{/*  */}
					</NCButton>
				</NCModal.Footer>
			</NCModal>
		);
	}
}
