import React, { Component } from 'react';
import { base, high } from 'nc-lightapp-front';
const { NCFormControl, NCRadio, NCCheckbox, NCIcon } = base;
const { Refer } = high;
const { ReferLoader } = Refer;
import UseridsRefer from '../../../public/uap/index.js';
import Utils from '../utils';
const { langCheck } = Utils;
require('./index.less');
let now = new Date();
now.setTime(now.getTime());

const CheckboxGroup = NCCheckbox.CheckboxGroup;
export default class TaskForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };
  }

  render() {
    const MSGVARS = {
      0: `${langCheck('reportTimeMultiLang', '100303-000012')}${now.getFullYear() + "-" + (now.getMonth() + 1) + "-" + now.getDate()}`, //
      1: `${langCheck('reportTimeMultiLang', '100303-000016')}${now.getFullYear()}`, //
      2: `${langCheck('reportTimeMultiLang', '100303-000015')}${now.getMonth() + 1}`, //
      3: `${langCheck('reportTimeMultiLang', '100303-000014')}${now.getDate()}`, //
      4: `${langCheck('reportTimeMultiLang', '100303-000017')}${now.getHours() + ":" + (now.getMinutes()) + ":" + now.getSeconds()}`, //
      5: `${langCheck('reportTimeMultiLang', '100303-000047')}`, //
      6: `${langCheck('reportTimeMultiLang', '100303-000048')}`, //
      7: `${langCheck('reportTimeMultiLang', '100303-000049')}` //
    };
    const { status, pageData } = this.props;
    const {
      taskName,
      taskNote,
      timeConfig,
      receivers,
      fileType,
      taskExecRoom,
      msgTitle,
      msgVars,
      msgText } = pageData;
    let receiversText = '';
    receivers && typeof (receivers) != 'string' && receivers.map(item => {
      if (item && item.refname) {
        receiversText += `${item.refname},`
      }
    })
    let varsText = '';
    msgVars && msgVars.map(item => {
      if (item) {
        varsText += `${MSGVARS[item]},`
      }
    })
    return (
      <div className="task-form">
        <div className="box large">
          <span className="lable">{status == 'edit' && <NCIcon type="uf-mi" className='mast'></NCIcon>}{langCheck('reportTimeMultiLang', '100303-000000')}:</span> {/** */}
          {status == 'edit' ? <NCFormControl value={taskName} onChange={val => this.props.onChange('taskName', val)} /> : <span>{taskName}</span>}
        </div>
        <div className="box large">
          <span className="lable">{langCheck('reportTimeMultiLang', '100303-000001')}:{/** */}</span>
          {status == 'edit' ? <NCFormControl value={taskNote} onChange={val => this.props.onChange('taskNote', val)} /> : <span>{taskNote}</span>}
        </div>
        <h3 className="title">{langCheck('reportTimeMultiLang', '100303-000002')}{/**/}</h3>
        <div className="box">
          <div className="info">
            <span className="lable">{status == 'edit' && <NCIcon type="uf-mi" className='mast'></NCIcon>}{langCheck('reportTimeMultiLang', '100303-000003')}:{/** */}</span>
            {status == 'edit' ?
              <div className="task-time" onClick={this.props.showTimeModal}>
                {timeConfig.occurType == '3' ? `${langCheck('reportTimeMultiLang', '100303-000050')}` : timeConfig.occurType == '4' ? `${langCheck('reportTimeMultiLang', '100303-000035')}` : timeConfig.occurType == '5' ? `${langCheck('reportTimeMultiLang', '100303-000015')}` : ''}
                <i className="icon iconfont icon-canzhaozuixin"></i>
              </div>
              : <span>{timeConfig.occurType == '3' ? `${langCheck('reportTimeMultiLang', '100303-000050')}` : timeConfig.occurType == '4' ? `${langCheck('reportTimeMultiLang', '100303-000035')}` : timeConfig.occurType == '5' ? `${langCheck('reportTimeMultiLang', '100303-000015')}` : ''}</span>}
          </div>
          <div className="info">
            <span className="lable">{status == 'edit' && <NCIcon type="uf-mi" className='mast'></NCIcon>}{langCheck('reportTimeMultiLang', '100303-000004')}:</span> {/** */}
            {status == 'edit' ? <UseridsRefer
              queryCondition={{
                isAllUserVisible: true,
                showSysAdmin: 'true',
                showGroupAdmin: 'true'
              }}
              value={receivers}
              onChange={val => this.props.onChange('receivers', val)}
            /> : <span>{receiversText}</span>}</div>
        </div>
        <div className="box">
          <span className="lable">{langCheck('reportTimeMultiLang', '100303-000005')}:</span>{/** */}
          {status == 'edit' ? <NCRadio.NCRadioGroup name="fileType" selectedValue={fileType} onChange={val => this.props.onChange('fileType', val)}>
            <NCRadio value={'0'}>Excle</NCRadio>
            <NCRadio value={'1'}>PDF</NCRadio>
            <NCRadio value={'2'}>CSV</NCRadio>
          </NCRadio.NCRadioGroup> : <span>{fileType == 0 ? 'Excle' : fileType == 1 ? 'PDF' : fileType == 2 ? 'CSV' : ''}</span>}
        </div>
        <h3 className="title">{langCheck('reportTimeMultiLang', '100303-000006')}</h3>{/** */}
        <div className="box">
          <div className="info">
            <span className="lable">{langCheck('reportTimeMultiLang', '100303-000007')}:</span>{/** */}
            {status == 'edit' ? <ReferLoader refcode="uapbd/refer/pubinfo/TimezoneDefaultGridRef/index" value={taskExecRoom.exectimezone} onChange={val => this.props.onChange('exectimezone', val)} /> : <span>{taskExecRoom.exectimezone && taskExecRoom.exectimezone.refname}</span>}
          </div>
          <div className="info">
            <span className="lable">{langCheck('reportTimeMultiLang', '100303-000008')}:</span>{/** */}
            {status == 'edit' ? <ReferLoader refcode="uap/refer/riart/multiLangDefaultTableRef/index" value={taskExecRoom.execlanguage} onChange={val => this.props.onChange('execlanguage', val)} /> : <span>{taskExecRoom.execlanguage && taskExecRoom.execlanguage.refname}</span>}
          </div>
        </div>
        <div className="box">
          <span className="lable">{langCheck('reportTimeMultiLang', '100303-000009')}:</span>{/** */}
          {status == 'edit' ? <NCRadio.NCRadioGroup name="isCurUser" selectedValue={taskExecRoom.isCurUser} onChange={val => this.props.onChange('isCurUser', val)}>
            <NCRadio value={'0'}>{langCheck('reportTimeMultiLang', '100303-000010')}</NCRadio>{/** */}
            <NCRadio value={'1'}>{langCheck('reportTimeMultiLang', '100303-000004')}</NCRadio>{/** */}
          </NCRadio.NCRadioGroup> : <span>{taskExecRoom.isCurUser == '0' ? `${langCheck('reportTimeMultiLang', '100303-000010')}` : `${langCheck('reportTimeMultiLang', '100303-000004')}`}</span>}
        </div>
        <div className="box large">
          <span className="lable">{langCheck('reportTimeMultiLang', '100303-000011')}:</span>{/** */}
          {status == 'edit' ? <NCFormControl value={msgTitle} onChange={val => this.props.onChange('msgTitle', val)} /> : <span>{msgTitle}</span>}
        </div>

        {status == 'edit' && <div className="box checkbox">
          <CheckboxGroup value={msgVars} onChange={val => this.props.onChange('msgVars', val)}>
            <NCCheckbox colors="primary" value="7">{langCheck('reportTimeMultiLang', '100303-000004')}</NCCheckbox>{/** */}
            <NCCheckbox colors="primary" value="0">{langCheck('reportTimeMultiLang', '100303-000012')}</NCCheckbox>{/** */}
            <NCCheckbox colors="primary" value="6">{langCheck('reportTimeMultiLang', '100303-000013')}</NCCheckbox>{/** */}
            <NCCheckbox colors="primary" value="3">{langCheck('reportTimeMultiLang', '100303-000014')}</NCCheckbox>{/** */}
            <NCCheckbox colors="primary" value="2">{langCheck('reportTimeMultiLang', '100303-000015')}</NCCheckbox>{/** */}
            <NCCheckbox colors="primary" value="1">{langCheck('reportTimeMultiLang', '100303-000016')}</NCCheckbox>{/** */}
            <NCCheckbox colors="primary" value="4">{langCheck('reportTimeMultiLang', '100303-000017')}</NCCheckbox>{/** */}
            <NCCheckbox colors="primary" value="5">{langCheck('reportTimeMultiLang', '100303-000018')}</NCCheckbox>{/** */}
          </CheckboxGroup>
        </div>}
        <div className="box large">
          <span className="lable">{langCheck('reportTimeMultiLang', '100303-000019')}:</span>{/** */}
          <span>{varsText}</span>
        </div>

        <div className="box large">
          <span className="lable">{langCheck('reportTimeMultiLang', '100303-000020')}:</span>{/** */}
          {status == 'edit' ? <NCFormControl value={msgText} onChange={val => this.props.onChange('msgText', val)} /> : <span>{msgText}</span>}
        </div>
      </div>
    );
  }
}
